@import url(https://fonts.googleapis.com/css?family=Jura);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    font-family: 'Jura', sans-serif;
}
/* remove scrollbar */
body::-webkit-scrollbar {
    width: 0;
}

.highlight-text {
    background: none;
    color: #f18911;
    font-weight: bold;
    padding: unset;
}

.jumbotron_mod {
    border-radius: 10px;
    margin-top: 20px;
    margin-bottom: 20px;
    background-color: white;
    height: 150px;
}

.jumbotron {
    padding: 10px;
}

.main-label {
    text-align: center;
}

.badge-mod {
    padding: 5px;
    margin-left: 10px;
    margin-bottom: 5px;
    margin-top: 5px;
}

.list-text_mod {
    margin-left: 20px;
    color: #2fb431;
    font-weight: 600;
}

.breadcrumb:hover {
    background-color: #dbdee1;
}

.item-number_mod {
    margin-right: 30px;
}

.form-mainLabel_mod {
    font-size: 3rem;
    padding: 0;
    margin: unset;
}

.form-button_mod {
    padding: 10px;
    margin-top: 20px;
    margin-bottom: 20px;
}

::-webkit-input-placeholder {
    font-size: 1.3rem;
    font-style: italic;
}

::-moz-placeholder {
    font-size: 1.3rem;
    font-style: italic;
}

:-ms-input-placeholder {
    font-size: 1.3rem;
    font-style: italic;
}

::-ms-input-placeholder {
    font-size: 1.3rem;
    font-style: italic;
}

::placeholder {
    font-size: 1.3rem;
    font-style: italic;
}

.jumbotron-input_mod {
    border-color: white;
    text-align: center;
}

.jumbotron-input-one_mod {
    text-align: right;
    font-weight: bolder;
    color: #2fb431;
}

.jumbotron-input-two_mod {
    text-align: right;
    font-weight: bolder;
}

.input-group {
    font-size: .5rem;
    outline: #f18911;
}

.jumbotron-button_mod {
    border-radius: 5px;
}

#useCaseInput, #runThisUCInput, #runAllInput {
    outline: none;
    border: none;
}

.jumbotron-button_mod {
    visibility: hidden;
}

.jumbotron-input_mod:hover + .jumbotron-button_mod, .jumbotron-button_mod:hover {
    visibility: visible;
}

.jumbotron-label_mod{
    font-size: .8rem;
    padding-top: 6px;
    color: #cc7610;
    font-weight: bolder;
}

.jumbotron-lead {
    color: #2fb431;
    font-size: 1.3rem;
    font-weight: bolder;
}

